.homepage {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  /* background-color: #60ddf0; */
  @media screen and (max-width: 550px) {
    padding: 25px;
  }
}

.homepagetitle {
  max-width: 400px;
  padding-left: 15px;
  font-size: 32px;
  font-weight: 700;
  margin-top: 30px;
  text-align: center;
  @media screen and (max-width: 550px) {
    font-size: 26px;
  }
}